@import url(https://fonts.googleapis.com/css2?family=Liu+Jian+Mao+Cao&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.navigation {
  background-color: var(--primatyColor);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
}
.navigation__logo {
  color: #fff;
}
.navigation__text {
  font-family: var(--fontFamilyVariant);
  /* margin-left: 3rem; */
  font-size: calc(1rem * 2);
}
.navigation__menu {
  color: #fff;
}
.navigation__menu-list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  list-style: none;
}
.navigation__menu-item {
  border-right: #fff 1px solid;
}
.navigation__menu-item > a {
  color: #fff;
  padding-right: 10px;
  text-decoration: none;
  text-transform: capitalize;
  font-weight: var(--fontWeightSemiBold);
}

.navigation__menu-item:last-child {
  border-right: none;
}

.header {
  /* width: 100vw; */
}
.header__image {
  width: 100%;
  aspect-ratio: 2 / 1;
  object-fit: fill;
}
.box__service {
  position: relative;
  width: 100%;
  height: calc(1rem + 11vw);
  background-color: #005f94;
}
.box__service-title {
  width: 90%;
  margin: 0rem auto;
  color: #89acc0;
  font-size: calc(1rem + 8vw);
  font-weight: var(--fontWeightMedium);
  padding: calc(1rem + 2vw);
}
.box__service-arrow {
  width: calc(100vw - 65vw);
  position: absolute;
  top: 41%;
  left: calc(1rem + 59vw);
  -webkit-transform: rotateZ(18deg);
          transform: rotateZ(18deg);
  z-index: 1;
}
.box__service__img {
  width: 80%;
  max-width: 80%;
  object-fit: contain;
  border: #000 solid 2px;
  margin-top: 3rem;
}
.box__service__title {
  font-weight: var(--fontWeightBold);
  font-size: calc(1rem + 1vw);
  text-align: center;
}
.content__service {
  width: calc(1rem + 77vw);
  margin: 5rem;
}
.content__text {
  font-weight: var(--fontWeightUltraLight);
  font-size: calc(1rem + 0.5vw);
}
.content__box__service {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(calc(1rem + 5vw), 1fr));
  grid-gap: calc(1rem + 1vw);
  /* margin: calc(1rem + 1vw) 0; */
}

.content__box__service-item {
  height: calc(1rem + 35vw);
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  border: #000 solid 1px;
  grid-gap: calc(1rem + 1vw);
  gap: calc(1rem + 1vw);
}

:root {
  --fontSize: 18px;
  --fontFamily: "Montserrat";
  --fontFamilyVariant: "Liu Jian Mao Cao", cursive;
  --fontWeight: 400;
  --fontWeightBold: 700;
  --fontWeightUltraBold: 800;
  --fontWeightLight: 300;
  --fontWeightMedium: 500;
  --fontWeightSemiBold: 600;
  --fontWeightThin: 100;
  --fontWeightUltraLight: 200;
  --primatyColor: #005f94;
  --smScreen: 640px;
  --lgScreen: 1024px;
  --xlScreen: 1440px;
}
